@import url("https://fonts.googleapis.com/css2?family=Anek+Tamil&display=swap");
@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Serif:ital,wght@1,200&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Anek+Tamil&display=swap");
.card-body,
.container .card-custom {
  background-color: #110307 !important;
}

:root {
  background-color: #202020 !important;
}

.navbar-brand,
h1,
.link,
.titles, .titles-expand {
  color: #cf6dde !important;
  font-family: "Lobster", cursive !important;
}
.navbar-brand .custom-icon,
h1 .custom-icon,
.link .custom-icon,
.titles .custom-icon, .titles-expand .custom-icon {
  color: #b447cc !important;
}

.titles-expand:hover {
  color: #92461a !important;
}

.custom-form-label {
  color: #000000 !important;
  background-color: #e6e6e6 !important;
  font-weight: 700 !important;
}

.navbar-brand,
.nav-link,
.custom-card-title {
  font-size: 1.7rem !important;
}

.navbar-brand {
  font-size: 4rem !important;
}

p,
.card-text {
  color: #eeeeee;
}

.app-container {
  background-color: #202020;
  font-family: "Anek Tamil", sans-serif !important;
}

.custom-btn {
  background-color: #f8d6fa !important;
  border-color: #f8d6fa !important;
  color: #141414 !important;
}

.custom-btn:hover {
  background-color: #cf6dde !important;
}

.custom-btn-delete {
  background-color: #c10088 !important;
  border-color: #c10088 !important;
}

.custom-btn-delete:hover {
  background-color: #7e1d53 !important;
}

.card {
  border: 2px solid #b447cc !important;
}